import { createWebHistory, createRouter } from "vue-router";

import Forsida from '@/components/Forsida'
import Upplysingar from '@/components/Upplysingar'
import Skrainn from '@/components/Skrainn'
import UserPage from '@/components/Userpage'
import ErrorPage from '@/components/Errorpage'

const routes = [
  {
    path: '/Utilegan/',
    name: 'Forsida',
    component: Forsida
  },
  {
    path: '/Utilegan/upplysingar',
    name: 'Upplysingar',
    component: Upplysingar
  },
  {
    path: '/Utilegan/skra-inn',
    name: 'Skra-inn',
    component: Skrainn
  },
  {
    path: '/Utilegan/notandi',
    name: 'User',
    component: UserPage
  },
  {
    path: '/Utilegan/:pathMatch(.*)*',
    name: 'Error',
    component: ErrorPage
  },
];


const router = createRouter({
  history: createWebHistory(),
  routes,
  scrollBehavior() {
    document.getElementById('app').scrollIntoView({behavior: "smooth"});
}
});

export default router;
